<template>
  <header>
    <MainHeader />
  </header>

  <div classs="mainContainer">

    <div class="navContainer">
      <h2 v-if="toogleProjects" class="navBtn" @click="mainToogleSwitch()">Voir mon CV</h2>
      <h2 v-if="toogleCv" class="navBtn" @click="mainToogleSwitch()">Voir mes projets</h2>
    </div>
    <div v-if="toogleCv">
      <CurriculumVitae />
    </div>
    <div v-if="toogleProjects">
      <ProjectsList :cardsDataPro="projectsProData" :cardsDataSolo="projectsSolo" />
    </div>

    <footer>
      <MainFooter :toogleCv="toogleCv" />
    </footer>
  </div>
</template>

<script>
import CurriculumVitae from "./components/CurriculumVitae.vue";
import ProjectsList from './components/ProjectsList.vue';
import MainHeader from './components/MainHeader.vue';
import projectsProData from './projectsProData.json';
import projectsSolo from './projectsSolo.json';
import MainFooter from "./components/MainFooter.vue";

export default {
  components: {
    CurriculumVitae,
    ProjectsList,
    MainHeader,
    MainFooter
  },
  data() {
    return {
      toogleCv: true,
      toogleProjects: false,
      projectsProData: []
    }
  },
  methods: {
    mainToogleSwitch() {
      this.toogleCv = !this.toogleCv;
      this.toogleProjects = !this.toogleProjects;
    },
    fetchProjectsData() {
      this.projectsProData = projectsProData;
      this.projectsSolo = projectsSolo;
    }
  },
  created() {
    this.fetchProjectsData();
  }
}

</script>

